import { DispatchWithoutAction, FC, useState } from 'react';
import logo from './logo.svg';
import './App.css';
import { useThemeParams, WebAppProvider } from '@vkruglikov/react-telegram-web-app';
import useBetaVersion from './useBetaVersion';
import { ConfigProvider, theme } from 'antd';
import Expand from './components/Expand';
import MainButton from './components/MainButton';
import BackButton from './components/BackButton';
import ShowPopup from './components/ShowPopup';
import HapticFeedback from './components/HapticFeedback';
import ScanQrPopup from './components/ScanQrPopup';

const TGApp: FC<{
  onChangeTransition: DispatchWithoutAction;
}> = ({ onChangeTransition }) => {
  const [colorScheme, themeParams] = useThemeParams();
  const [isBetaVersion, handleRequestBeta] = useBetaVersion(false);
  const [activeBtn, setActiveBtn] = useState(true);

  return (
    <div>
      <ConfigProvider
        theme={
          themeParams.text_color
            ? {
                algorithm:
                  colorScheme === 'dark'
                    ? theme.darkAlgorithm
                    : theme.defaultAlgorithm,
                token: {
                  colorText: themeParams.text_color,
                  colorPrimary: themeParams.button_color,
                  colorBgBase: themeParams.bg_color,
                },
              }
            : undefined
        }
      >
        <header className="App-header">
          <img
            onClick={handleRequestBeta}
            src={logo}
            className="App-logo"
            alt="logo"
          />
        </header>
        <div className="contentWrapper">
          {isBetaVersion && (
            <div className="betaVersion">
              <h3>WARNING: BETA VERSION</h3>
              <button onClick={() => setActiveBtn(state => !state)}>
                change button
              </button>
              <button onClick={onChangeTransition}>change </button>
            </div>
          )}
          <Expand />
          {!activeBtn ? (
            <MainButton
              initialValues={{
                show: isBetaVersion,
                text: 'SECOND BUTTON',
                progress: true,
              }}
              key="1"
            />
          ) : (
            <MainButton
              key="2"
              initialValues={{
                show: isBetaVersion,
              }}
            />
          )}
          <BackButton />
          <ShowPopup />
          <HapticFeedback />
          <ScanQrPopup />
        </div>
      </ConfigProvider>
    </div>
  );
};

function App () {
  const [smoothButtonsTransition, setSmoothButtonsTransition] = useState(false);
  return (
    <WebAppProvider options={{ smoothButtonsTransition }}>
      <TGApp
        onChangeTransition={() => setSmoothButtonsTransition(state => !state)}
      />
    </WebAppProvider>
  );
}

export default App;